@include font('bebas_neue_regularregular',bebasneue_regular-webfont);

@mixin font {
	font-family: bebas_neue_regularregular, sans-serif; }
img {
	width: 100%; }

.maske {
	position: fixed;
	z-index: 2000;
	// overflow: hidden
	width: 100%;
	height: 100%;
	background: #000;
	mix-blend-mode: darken;
	// animation-name: slide
	// animation-duration: 3s
	// animation-fill-mode: forwards
	// @keyframes slide
	// 	99%
	// 		height: 100vh
	// 	100%
	// 		height: 0vh
	&__inner {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 150vw;
		height: 150vh;
		transform: translate(-50%, -50%) scale(0.01);
		border-radius: 50%;
		background: #fff;
		border-radius: 50%;
