 } }		// 		transform: translate(-50%, -50%) scale(1)

.wrapper {
	max-width: 1400px;
	overflow: hidden;
	margin: 0 auto;
	position: relative;
	padding-bottom: 41px;
	background: url('../img/bg.jpg') repeat; }

.container {
	max-width: 940px;
	margin: 0 auto;
	// overflow: hidden
	@include clr;
	@include r(960) {
		margin: 0 30px; } }

.header {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 100;
	.header__left {
		float: left;
		@include r(485) {
			float: none;
			text-align: center; } }
	.header__right {
		float: right;
		@include r(485) {
			float: none; } } }

.logo {
	@include font;
	// font-family: 'bebas_neue_regularregular'
	font-size: 36px;
	color: #e8603c;
	display: block;
	text-transform: uppercase;
	text-decoration: none;
	padding: 15px 0px;
	&__color {
		color: #f3af9d; } }

.nav {
	list-style: none;
	text-align: right;
	position: relative;
	@include r(420) {
		text-align: center; }
	&__item {
		display: inline-block;
		@include r(420) {
			text-align: center; } }
	&__link {
		@include font;
		text-decoration: none;
		display: block;
		color: #b9b9b9;
		// font-family: 'bebas_neue_regularregular'
		font-size: 16px;
		font-weight: 400;
		padding: 25px 0 27px 28px;
		@include r(420) {
			padding: 0 10px; } }
	.is-active {
		color: #e8603c; } }

.blog {
	background-color: rgba(232, 96, 60, 0.5);
	height: 61px;
	margin: 70px 0 24px;
	@include r(490) {
		margin: 120px 0 24px; } }

.blog-content {
	background-color: #e8603c;
	// max-width: 940px
	box-shadow: 0 0 0 10px #fff;
	height: 61px; }

.h1 {
	@include font;
	color: white;
	// font-family: 'bebas_neue_regularregular'
	font-size: 24px;
	font-weight: 400;
	padding: 17px 0 18px 17px; }

