.post {
	margin-bottom: 57px; }

.section {
	&__left {
		float: left;
		max-width: 670px;
		position: relative;
		@include r(965) {
			float: none;
			margin: 0 auto; } }
	&__right {
		float: right;
		@include r(965) {
			float: none;
			text-align: center;
			margin-top: 40px; } }
	&__photo {
		margin-bottom: 23px; } }

.icons {
	position: absolute;
	left: -85px;
	top: 0;
	text-align: center;
	// padding: 16px 0
	width: 75px;
	height: 75px;
	background: #e8603c;
	transition: 2s;
	&:hover {
		border-radius: 50%;
		transform: rotate(360deg); }
	@include r(1130) {
		position: static;
		margin-bottom: 20px;
		@include r(650) {
			width: 53px;
			height: 53px;
 } }			// padding: 5px 0
	&_post-first {
		background: url('../img/icon-first.png') #e8603c  no-repeat center; }
	&_post-second {
		background: url('../img/icon2.png') #e8603c  no-repeat center; }
	&_quotes-white {
		background: url('../img/quotes-white.png')  #e8603c  no-repeat center; }
	// &_link
	// 	background: #e8603c  no-repeat center
	// 	transition: 2s
	// 	&:hover
 }	// 		transform: rotate(360deg)

.icon-link {
	width: 36px;
	height: 43px;
	position: relative;
	top: 50%;
	transform: translateY(-50%); }

.content {
	overflow: hidden;
	padding-right: 10px;
	// padding-bottom: 48px
	position: relative;
	&:after {
		content: "";
		display: block;
		background: url('../img/bgdarken.png');
		height: 13px;
		width: calc(100% - 9px);
		// width: 100%
		position: absolute;
		bottom: 0;
		/* flex-grow: 1; */ }
	&_post-padd {
		padding-bottom: 65px; }
	&_feedback-padd {
		padding-bottom: 54px; }
	&_freebies-padd {
		padding-bottom: 62px; }
	&__left {
		float: left;
		position: relative;
		@include r(705) {
			float: left; } }
	&__right {
		float: left;
		max-width: 500px;
		margin-left: 51px; } }

.info {
	margin-bottom: 16px;
	font-size: 12px;
	font-weight: 400;
	@include r(705) {
		display: inline-block;
		text-align: center;
		margin: 0 20px 20px 0; }
	&__description {
		color: #bababa;
		display: block;
		padding-top: 10px;
		text-decoration: none;
		// span
 } }		// 	font-weight: 300

.h3 {
	@include font;
	font-size: 12px;
	font-weight: 400;
	// font-family: 'bebas_neue_regularregular'
	color: #e8603c;
	&_size {
		font-size: 14px; } }

.article {
	font-weight: 400;
	&__title {
		margin-bottom: 23px; } }

.h2 {
	@include font;
	font-size: 18px;
	color: #e8603c; }

.text {
	color: #414141;
	font-size: 12px;
	font-weight: 400;
	font-family: Helvetica,arial,sans-serif;
	line-height: 18px;
	&__dots {
		color: #8caf2f;
		font-weight: 500; }
	&__number {
		font-weight: 500; }
	&_margin {
		margin-bottom: 25px; } }

.features {
	max-width: 218px;
	margin-bottom: 37px;
	@include r(956) {
		display: inline-block;
		vertical-align: top;
		margin: 10px; } }

.list {
	background: url('../img/bgdarken.jpg') repeat;
	overflow: hidden;
	margin-bottom: 21px;
	&__title {
		padding-right: 20px;
		float: left;
		background: url('../img/bg.jpg') repeat; } }

.link {
	.link__item {
		color: #464646;
		display: block;
		text-decoration: none; } }

.gallery {
	font-size: 0;
	margin: 19px -3px 0;
	&__block {
		display: inline-block;
		width: 50px;
		height: 50px;
		border: 1px solid #e8603c;
		background-color: #f3af9d;
		margin: 0 3px 5px; } }

