.feedback {
	margin-top: 47px;
	&__text {
		background-color: whitesmoke;
		padding: 25px 30px 19px 76px;
		position: relative;
		box-sizing: border-box; } }

.textual {
	color: #474747;
	font-family: Helvetica,arial,sans-serif;
	font-size: 13px;
	font-style: italic;
	line-height: 18px; }

.icon-quotes {
	position: absolute;
	left: 34px;
	top: 20px;
	z-index: 10; }

.freebies {
	margin-top: 47px;
	&__link {
		font-family: Helvetica,arial,sans-serif;
		display: block;
		text-decoration: none;
		color: #a0a0a0;
		margin: 10px 0 25px;
		font-size: 12px;
		font-weight: 400; } }

.page-number {
	@include r(965) {
		text-align: center; }
	&__item {
		@include font;
		display: inline-block;
		width: 26px;
		height: 26px;
		text-align: center;
		line-height: 26px;
		text-decoration: none;
		color: white;
		text-align: center;
		font-size: 14px;
		font-weight: 400;
		background-color: #f3b29d;
 }		// font-family: 'bebas_neue_regularregular'
	.is-active {
		background-color: #e8663c; } }

.slider {
	max-width: 670px; }

.slider .slick-prev {
	background: url('../img/previous.png') no-repeat;
	background-size: cover;
	z-index: 100;
	width: 35px;
	height: 52px;
	left: 18px;
	@include r(550) {
		width: 15px;
		height: 24px;
		background-size: cover; } }

.slider .slick-prev:before,
.slider .slick-next:before {
	content: ''; }

.slider:before {
	content: ''; }

.slider .slick-next {
	right: 18px;
	background: url('../img/next.png') no-repeat;
	background-size: cover;
	z-index: 100;
	width: 35px;
	height: 52px;
	@include r(550) {
		width: 15px;
		height: 24px;
		background-size: cover; } }

.slider .slick-next:focus, .slider .slick-next:hover {
	background: url('../img/next.png');
	background-size: cover;
	@include r(550) {
		width: 15px;
		height: 24px;
		background-size: cover; } }

.slider .slick-prev:focus, .slider .slick-prev:hover {
	background: url('../img/previous.png');
	@include r(550) {
		width: 15px;
		height: 24px;
		background-size: cover; } }
